<template>
	<div>
		<Button :icon="require('@/assets/icons/1.png')" />
		<img class="hero__left-block_dot" src="@/assets/icons/dot.png" alt="" />
		<Button :icon="require('@/assets/icons/2.png')" />
		<img class="hero__left-block_dot" src="@/assets/icons/dot.png" alt="" />
		<Button :icon="require('@/assets/icons/3.png')" />
		<img class="hero__left-block_dot" src="@/assets/icons/dot.png" alt="" />
		<Button :icon="require('@/assets/icons/4.png')" />
	</div>
</template>

<script>
import { ref, onMounted, computed } from 'vue';
import Button from '../components/Button.vue';
import { gsap } from 'gsap';
import { useStore } from 'vuex';
export default {
	components: {
		Button,
	},
	setup() {
		const isLoading = computed(() => store.getters.checkLoading);

		const setIsLoading = () => {
			store.dispatch('setIsLoading', true);
		};
		onMounted(() => {
			if (isLoading) {
				setTimeout(() => {
					gsap.fromTo(
						'.hero__left-block',
						{
							right: '-20vw',
						},
						{ duration: 1, right: 0, ease: 'linear' }
					);
				}, 3000);
			}
		});

		return { isLoading, setIsLoading };
	},
};
</script>

<style lang="scss" scoped></style>
