<template>
	<div class="works">
		<div class="works__wrapper" ref="container">
			<Project
				class="works__project"
				v-for="(project, index) in projects"
				:key="index"
				:project="project"
			/>
		</div>
		<Sphere class="works__sphere" />
	</div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { gsap } from 'gsap';
import { CSSPlugin } from 'gsap/CSSPlugin';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Sphere from '../components/Sphere.vue';
import Project from './Project.vue';
import firstImage from '../assets/Projects/1.jpg';
gsap.registerPlugin(CSSPlugin);
gsap.registerPlugin(ScrollTrigger);
export default {
	components: {
		Project,
		Sphere,
	},
	setup() {
		const container = ref(null);
		const projects = [
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
		];

		onMounted(() => {
			gsap.to('.works__sphere', {
				scrollTrigger: {
					trigger: '.works__sphere',
					start: 'start start',
					end: 'top -600vh',
					scrub: 3,
					pin: true,
				},
				top: '0%',
				duration: 10,
				ease: 'linear',
			});
			gsap.set(container.value, { perspective: 40000 });
			const tl = gsap.timeline({
				scrollTrigger: {
					trigger: '.works__project',
					start: 'top center',
					end: 'bottom top',
					scrub: 4,
				},
			});
			tl.fromTo(
				container.value,
				{
					rotationY: '-20deg',
					left: '10vw',
					top: '-25vh',
					opacity: 1,
					zIndex: 12,
				},
				{
					duration: 5,
					rotationY: '0deg',
					left: '-100vw',
					top: '5vh',
					ease: 'linear',
					opacity: 1,
					zIndex: 12,
				}
			);
			tl.fromTo(
				container.value,
				{
					rotationY: '0deg',
					left: '-100vw',
					top: '5vh',
					opacity: 1,
					zIndex: 12,
				},
				{
					duration: 5,
					rotationY: '20deg',
					left: '-230vw',
					top: '5vh',
					ease: 'linear',
					opacity: 1,
					zIndex: 12,
				}
			);
			gsap.to(container.value, { duration: 1, z: '50000px' });
		});

		return { container, projects };
	},
};
</script>

<style lang="scss">
.works {
	position: relative;
	&__wrapper {
		transform: rotatez(15deg);
		width: 150vw;
		height: 150vh;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-around;
		gap: 150px;
		z-index: 1;
		margin-left: 100vw;
		left: 10vw;
		top: -25vh;
	}
	&__sphere {
		z-index: 10;
		position: absolute;
		top: 0vh;
		margin: 0;
		padding: 0;
	}
	&__project {
		top: 0;
		position: relative;
		line-height: 100px;
		text-align: center;
	}
}
</style>
