<template>
	<div ref="projectWrapper" class="project__wrapper">
		<img class="project__image" :src="$props.project.img" alt="" />
		<div class="project__text-box">
			<p class="project__description">
				{{ $props.project.description }}
			</p>
			<span v-for="tag in $props.project.tags" class="project__tags">
				<span :class="'project__tag project__tag_' + tag">{{ tag }}  </span>
			</span>
		</div>
	</div>
</template>

<script>
import { ref, onMounted, computed } from 'vue';
import gsap from 'gsap';
export default {
	props: {
		project: {
			img: String,
			description: String,
			tags: Array,
		},
	},
	setup(props) {
		const projectWrapper = ref(null);
		onMounted(() => {
			const tl = gsap.timeline({
				scrollTrigger: {
					trigger: projectWrapper.value,
					start: 'top bottom',
					end: 'bottom top',
					scrub: 3,
				},
			});
			tl.to(projectWrapper.value, {
				duration: 1,

				z: '12320px',
			});
			tl.to(projectWrapper.value, {
				duration: 1,

				start: 'top 70vh',
				z: '0px',
			});
		});
		return { projectWrapper };
	},
};
</script>

<style lang="scss" scoped>
.project {
	font-family: 'Merriweather', serif;
	&__wrapper {
		border-radius: 20px;
		background-color: rgba(76, 140, 65, 0.6);
		width: 340px;
		padding: 20px;
	}

	&__image {
		object-fit: cover;
		border-radius: 20px;
		width: 300px;
		height: 160px;
		overflow: hidden;
	}

	&__text-box {
		font-size: 2.5rem;
		color: white;
		text-shadow: 1px 1px 4px black;
	}

	&__description {
	}

	&__tags {
	}
	&__tag {
		&_html {
		}
		&_css {
		}
		&_js {
		}
		&_ts {
		}
		&_scss {
		}
		&_gsap {
		}
	}
}
</style>
