<template>
	<div class="works">
		<div class="works__wrapper" ref="container">
			<div v-for="project in projects" :key="index" class="works__project">
				<Project :project="project" />
			</div>
		</div>
		<Sphere class="works__sphere" />
	</div>
</template>

<script>
import { ref, onMounted, computed } from 'vue';
import SolarSystem from '../components/SolarSystem.vue';
import Sphere from '../components/Sphere.vue';
import Project from '../components/Project.vue';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import gsap from 'gsap';
import firstImage from '../assets/Projects/1.jpg';
export default {
	name: 'HomeView',
	components: {
		Sphere,
		SolarSystem,
		Project,
	},
	setup() {
		gsap.registerPlugin(ScrollTrigger);
		const container = ref(null);
		const projects = [
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
			{
				name: 'first',
				img: firstImage,
				description: 'Описание',
				tags: ['html', 'CSS'],
			},
		];
		onMounted(() => {
			gsap.fromTo(
				'.works__sphere',
				{
					top: '0%',
				},
				{
					scrollTrigger: {
						trigger: '.works__sphere',
						start: 'start start',
						end: 'top -2500vh',
						scrub: 3,
						pin: true,
						markers: true,
					},
					top: '0%',
					duration: 10,
					ease: 'linear',
				}
			);
			gsap.set(container.value, { perspective: 40000 });
			const tl = gsap.timeline({
				scrollTrigger: {
					trigger: '.works__project',
					start: 'top bottom',
					end: 'bottom top',
					scrub: 4,
					markers: true,
				},
			});
			tl.fromTo(
				container.value,
				{
					rotationY: '-20deg',
					x: '2500px',
					y: '-200px',
				},
				{
					duration: 50,
					rotationY: '0deg',
					x: '400px',
					y: '50',
					ease: 'none',
				}
			);
			tl.fromTo(
				container.value,
				{
					rotationY: '0deg',
					x: '400px',
					y: '50',
				},
				{
					duration: 50,
					rotationY: '20deg',
					x: '-1600px',
					y: '0px',
					ease: 'none',
				}
			);

			gsap.to('.works__project', { duration: 1 });
			gsap.to('.container', { duration: 1, z: '50000px' });
			// const tl = gsap.timeline({
			// 	scrollTrigger: {
			// 		trigger: '.works__wrapper',
			// 		start: 'start 300px',
			// 		end: 'top -2400vh',
			// 		scrub: 4,
			// 		pin: true,
			// 	},
			// });
			// tl.fromTo(
			// 	'.works__wrapper',
			// 	{
			// 		opacity: 1,
			// 		left: '200vw',
			// 		top: '160vh',
			// 	},
			// 	{
			// 		opacity: 1,
			// 		duration: 2,
			// 		scale: 1.5,
			// 		left: '-100vw',
			// 		ease: 'linear',
			// 		top: '30vh',
			// 	}
			// );
			// tl.fromTo(
			// 	'.works__wrapper',
			// 	{
			// 		opacity: 1,
			// 		left: '-100vw',
			// 		top: '30vh',
			// 	},
			// 	{
			// 		opacity: 1,
			// 		duration: 2,
			// 		// scale: 1,
			// 		left: '-400vw',
			// 		ease: 'linear',

			// 		top: '-100vh',
			// 	}
			// );
		});
		return { projects, container };
	},
};
</script>

<style lang="scss" scoped>
.works {
	left: 0;
	position: relative;
	width: 100vw;
	height: 450vh;
	overflow: hidden;
	&__sphere {
		position: relative;
		top: 0%;
		width: 100vw;
		margin: 0;
		padding: 0;
	}
	&__wrapper {
		// opacity: 0;
		transform: rotatez(15deg);
		width: 150vw;
		top: 50vh;
		position: relative;
		// height: 100%;
		overflow: hidden;
		display: flex;
		justify-content: center;
		align-items: start;
		z-index: 20;
		gap: 200px;
	}

	&__project {
	}
}

